import React from "react"
import Layout from '../../components/Global/Layout/Layout'
import Breadcrumb from '../../components/Global/Pages/Breadcrumb'
import Header from '../../components/Global/App/Header/Header.component'
import AboutSection from '../../components/Local/Pages/About/About.component'
import OurTeam from '../../components/Global/Pages/OurTeam.component'
import Testimonials from '../../components/Local/Pages/About/Testimonials.about.component'
import OurPartners from '../../components/Global/OurPartners/OurPartners.component'
import StartPartners from '../../components/Global/StartPartners/StartPartners.component'
import Footer from '../../components/Global/App/Footer/Footer.component'
const About = () => {
	return (
		<Layout>
		<Header />		
        <Breadcrumb pageName='About Us'/>
		<AboutSection />
		<OurTeam />
		<Testimonials />
		<OurPartners />
		<StartPartners addClass="bgc-thm"/>
		<Footer />
		</Layout>
	);
}

export default About