import React, {useState, useEffect} from 'react'
import  { Link , useStaticQuery , graphql } from 'gatsby'
import Loadable from '@loadable/component'

const OwlCarousel = Loadable(() => import('react-owl-carousel3'))

const options = {
    loop: true,
    nav: true,
    dots: false,
    autoplayHoverPause: false,
    autoplay: false,
    margin: 30,
    smartSpeed: 1200,
    navText: [
      	'<i class="flaticon-left-arrow"></i>',
      	'<i class="flaticon-right-arrow"></i>'
    ],
    responsive: {
        0: {
            items: 1,
            center: false
        },
        480:{
            items:1,
            center: false
        },
        600: {
            items: 1,
            center: false
        },
        768: {
            items: 2
        },
        992: {
            items: 4
        },
        1200: {
            items: 4
        },
        1280: {
            items: 4
        }
    }
}
const SocialIcon=({facebook,twitter,instagram,pinterest,dribbble,google})=>{
	return(
		<div className="overylay">
			<ul className="social_icon">
				<li className="list-inline-item"><Link to={facebook}><i className="fa fa-facebook"></i></Link></li>
				<li className="list-inline-item"><Link to={twitter}><i className="fa fa-twitter"></i></Link></li>
				<li className="list-inline-item"><Link to={instagram}><i className="fa fa-instagram"></i></Link></li>
				<li className="list-inline-item"><Link to={pinterest}><i className="fa fa-pinterest"></i></Link></li>
				<li className="list-inline-item"><Link to={dribbble}><i className="fa fa-dribbble"></i></Link></li>
				<li className="list-inline-item"><Link to={google}><i className="fa fa-google"></i></Link></li>
			</ul>
		</div>
	)
}

const OurTeam = () => {
	const [display, setDisplay] = useState(false);

    useEffect(() => {
        setDisplay(true);
    }, [])
	const data = useStaticQuery(graphql`
	query{
		team1: file(relativePath: {eq: "team/1.jpg"}) {
			id
			childImageSharp {
			  fluid {
				originalImg
			  }
			}
		}
		team2: file(relativePath: {eq: "team/2.jpg"}) {
			id
			childImageSharp {
			  fluid {
				originalImg
			  }
			}
		}
		team3: file(relativePath: {eq: "team/3.jpg"}) {
			id
			childImageSharp {
			  fluid {
				originalImg
			  }
			}
		}
		team4: file(relativePath: {eq: "team/4.jpg"}) {
			id
			childImageSharp {
			  fluid {
				originalImg
			  }
			}
		}
	}`
)

	return (
		<section className="our-team bgc-f7">
			<div className="container">
				<div className="row">
					<div className="col-lg-6 offset-lg-3">
						<div className="main-title text-center">
							<h2>Our Team</h2>
							<p>Lorem ipsum dolor sit amet, consectetur adipiscing elit.</p>
						</div>
					</div>
				</div>
				<div className="row">
					<div className="col-lg-12">
						{display ? <OwlCarousel className="team_slider owl-carousel owl-theme" {...options}> 
						<div className="item">
							<div className="team_member">
								<div className="thumb">
									<img className="img-fluid" src={data.team1.childImageSharp.fluid.originalImg} alt="1.jpg"/>
									<SocialIcon 
										facebook="#"
										twitter="#"
										instagram="#"
										pinterest="#"
										dribbble="#"
										google="#"
									/>

								</div>
								<div className="details">
									<h4>Jennifer Barton</h4>
									<p>Broker</p>
								</div>
							</div>
						</div>
						<div className="item">
							<div className="team_member">
								<div className="thumb">
									<img className="img-fluid" src={data.team2.childImageSharp.fluid.originalImg} alt="2.jpg"/>
									
								</div>
								<div className="details">
									<h4>Kathleen Myers</h4>
									<p>Agent</p>
								</div>
							</div>
						</div>
						<div className="item">
							<div className="team_member">
								<div className="thumb">
									<img className="img-fluid" src={data.team3.childImageSharp.fluid.originalImg} alt="3.jpg"/>
									<SocialIcon 
										facebook="#"
										twitter="#"
										instagram="#"
										pinterest="#"
										dribbble="#"
										google="#"
									/>
								</div>
								<div className="details">
									<h4>Mariusz Ciesla</h4>
									<p>Broker</p>
								</div>
							</div>
						</div>
						<div className="item">
							<div className="team_member">
								<div className="thumb">
									<img className="img-fluid" src={data.team4.childImageSharp.fluid.originalImg} alt="4.jpg"/>
									<SocialIcon 
										facebook="#"
										twitter="#"
										instagram="#"
										pinterest="#"
										dribbble="#"
										google="#"
									/>
								</div>
								<div className="details">
									<h4>Helene Powers</h4>
									<p>Broker</p>
								</div>
							</div>
						</div>
						<div className="item">
							<div className="team_member">
								<div className="thumb">
									<img className="img-fluid" src={data.team1.childImageSharp.fluid.originalImg} alt="1.jpg"/>
									<SocialIcon 
										facebook="#"
										twitter="#"
										instagram="#"
										pinterest="#"
										dribbble="#"
										google="#"
									/>
								</div>
								<div className="details">
									<h4>Jennifer Barton</h4>
									<p>Broker</p>
								</div>
							</div>
						</div>
						<div className="item">
							<div className="team_member">
								<div className="thumb">
									<img className="img-fluid" src={data.team2.childImageSharp.fluid.originalImg} alt="2.jpg"/>
									<SocialIcon 
										facebook="#"
										twitter="#"
										instagram="#"
										pinterest="#"
										dribbble="#"
										google="#"
									/>
								</div>
								<div className="details">
									<h4>Kathleen Myers</h4>
									<p>Agent</p>
								</div>
							</div>
						</div>
						<div className="item">
							<div className="team_member">
								<div className="thumb">
									<img className="img-fluid" src={data.team3.childImageSharp.fluid.originalImg} alt="3.jpg"/>
									<SocialIcon 
										facebook="#"
										twitter="#"
										instagram="#"
										pinterest="#"
										dribbble="#"
										google="#"
									/>
								</div>
								<div className="details">
									<h4>Mariusz Ciesla</h4>
									<p>Broker</p>
								</div>
							</div>
						</div>
						<div className="item">
							<div className="team_member">
								<div className="thumb">
									<img className="img-fluid" src={data.team4.childImageSharp.fluid.originalImg} alt="4.jpg"/>
									<SocialIcon 
										facebook="#"
										twitter="#"
										instagram="#"
										pinterest="#"
										dribbble="#"
										google="#"
									/>
								</div>
								<div className="details">
									<h4>Helene Powers</h4>
									<p>Broker</p>
								</div>
							</div>
						</div>
                		</OwlCarousel> : ''}
					</div>
				</div>
			</div>
		</section>
	)
}

export default OurTeam