import React from 'react'
import  { useStaticQuery , graphql } from 'gatsby'


const OurPartners = () => {
	const data = useStaticQuery(graphql`
	query{
		partner1: file(relativePath: {eq:"partners/1.png"}) {
			id
			childImageSharp {
			  fluid {
				originalImg
			  }
			}
		}
		partner2: file(relativePath: {eq:"partners/2.png"}) {
			id
			childImageSharp {
			  fluid {
				originalImg
			  }
			}
		}
		partner3: file(relativePath: {eq:"partners/3.png"}) {
			id
			childImageSharp {
			  fluid {
				originalImg
			  }
			}
		}
		partner4: file(relativePath: {eq:"partners/4.png"}) {
			id
			childImageSharp {
			  fluid {
				originalImg
			  }
			}
		}
		partner5: file(relativePath: {eq:"partners/5.png"}) {
			id
			childImageSharp {
			  fluid {
				originalImg
			  }
			}
		}	
	}`
)
	return (
		<section id="our-partners" className="our-partners">
  			<div className="container">
			    <div className="row">
			      	<div className="col-lg-6 offset-lg-3">
			        	<div className="main-title text-center">
			          		<h2>Our Partners</h2>
			          		<p>We only work with the best companies around the globe</p>
			        	</div>
			      	</div>
			    </div>
    			<div className="row">
      				<div className="col-sm-6 col-md-4 col-lg">
				        <div className="our_partner">
				          	<img className="img-fluid" src={data.partner1.childImageSharp.fluid.originalImg} alt="1.png" />
				        </div>
      				</div>
      				<div className="col-sm-6 col-md-4 col-lg">
        				<div className="our_partner">
          					<img className="img-fluid" src={data.partner2.childImageSharp.fluid.originalImg} alt="2.png" />
        				</div>
      				</div>
      				<div className="col-sm-6 col-md-4 col-lg">
				        <div className="our_partner">
				          	<img className="img-fluid" src={data.partner3.childImageSharp.fluid.originalImg} alt="3.png" />
				        </div>
      				</div>
      				<div className="col-sm-6 col-md-4 col-lg">
				        <div className="our_partner">
				          	<img className="img-fluid" src={data.partner4.childImageSharp.fluid.originalImg} alt="4.png" />
				        </div>
      				</div>
      				<div className="col-sm-6 col-md-4 col-lg">
				        <div className="our_partner">
				          	<img className="img-fluid" src={data.partner5.childImageSharp.fluid.originalImg} alt="5.png" />
				        </div>
      				</div>
    			</div>
  			</div>
		</section>
	)
}

export default OurPartners