import React,{useState} from 'react'
import {graphql, useStaticQuery} from 'gatsby'
import ModalVideo from 'react-modal-video'
import 'react-modal-video/scss/modal-video.scss';
import styled from 'styled-components'
const IconConntainer= styled.div `
    background: #ff5a5f;
    border-radius: 100%;
    font-size: 22px;
    height: 70px;
    left: 0;
    line-height: 70px;
    margin: 0 auto;
    position: absolute;
    right: 0;
    text-align: center;
    top: 43%;
    width: 70px;
    color:#fff;
    &:after{
        background-color: rgba(255, 90, 95, 0.2);
        border-radius: 100%;
        content: "";
        height: 87px;
        left: -7px;
        position: absolute;
        top: -7px;
        width: 87px;
    }
`
const AboutSection =()=>{
    const [show, setShow] = useState(false);

    const data = useStaticQuery(graphql`
	query{
		about: file(relativePath: {eq: "about/1.jpg"}) {
			id
			childImageSharp {
			  fluid {
				originalImg
			  }
			}
        }}`)
 return(
        <section className="about-section">
        <div className="container">
            <div className="row">
                <div className="col-lg-6 offset-lg-3">
                    <div className="main-title text-center">
                        <h2 className="mt0">Our Mission Is To FindHouse</h2>
                    </div>
                </div>
            </div>
            <div className="row">
                <div className="col-lg-8 col-xl-7">
                    <div className="about_content">
                        <p className="large">Mauris ac consectetur ante, dapibus gravida tellus. Nullam aliquet eleifend dapibus. Cras sagittis, ex euismod lacinia tempor.</p>
                        <p>Lorem ipsum dolor sit amet, consectetur adipiscing elit. Quisque quis ligula eu lectus vulputate porttitor sed feugiat nunc. Mauris ac consectetur ante, dapibus gravida tellus. Nullam aliquet eleifend dapibus. Cras sagittis, ex euismod lacinia tempor, lectus orci elementum augue, eget auctor metus ante sit amet velit.</p>
                        <p>Maecenas quis viverra metus, et efficitur ligula. Nam congue augue et ex congue, sed luctus lectus congue. Integer convallis condimentum sem. Duis elementum tortor eget condimentum tempor. Praesent sollicitudin lectus ut pharetra pulvinar. Donec et libero ligula. Vivamus semper at orci at placerat.Placeat Lorem ipsum dolor sit amet, consectetur adipisicing elit. Quod libero amet, laborum qui nulla quae alias tempora.</p>
                        <ul className="ab_counting">
                            <li className="list-inline-item">
                                <div className="about_counting">
                                    <div className="icon"><span className="flaticon-user"></span></div>
                                    <div className="details">
                                        <h3>80,123</h3>
                                        <p>Customers to date</p>
                                    </div>
                                </div>
                            </li>
                            <li className="list-inline-item">
                                <div className="about_counting">
                                    <div className="icon"><span className="flaticon-home"></span></div>
                                    <div className="details">
                                        <h3>$74 Billion</h3>
                                        <p>In home sales</p>
                                    </div>
                                </div>
                            </li>
                            <li className="list-inline-item">
                                <div className="about_counting">
                                    <div className="icon"><span className="flaticon-transfer"></span></div>
                                    <div className="details">
                                        <h3>$468 Million</h3>
                                        <p>In Savings</p>
                                    </div>
                                </div>
                            </li>
                        </ul>
                    </div>
                </div>
                <div className="col-lg-4 col-xl-5">
                    <div className="about_thumb">
                        <img className="img-fluid w100" src={data.about.childImageSharp.fluid.originalImg} alt="1.jpg"/>
                        <ModalVideo channel='youtube' autoplay isOpen={show} videoId="oqNZOOWF8qM" onClose={() => setShow(false)} />                    
                        <IconConntainer onClick={() => setShow(true)}><i className="flaticon-play"></i></IconConntainer>
                    </div>
                </div>
            </div>
            <div className="row mt50">
                <div className="col-lg-6 offset-lg-3">
                    <div className="main-title text-center">
                        <h2>Why Choose Us</h2>
                        <p>We provide full service at every step.</p>
                    </div>
                </div>
            </div>
            <div className="row">
                <div className="col-md-6 col-lg-4 col-xl-4">
                    <div className="why_chose_us style2">
                        <div className="icon">
                            <span className="flaticon-high-five"></span>
                        </div>
                        <div className="details">
                            <h4>Trusted By Thousands</h4>
                            <p>Aliquam dictum elit vitae mauris facilisis at dictum urna dignissim donec vel lectus vel felis.</p>
                        </div>
                    </div>
                </div>
                <div className="col-md-6 col-lg-4 col-xl-4">
                    <div className="why_chose_us style2">
                        <div className="icon">
                            <span className="flaticon-home-1"></span>
                        </div>
                        <div className="details">
                            <h4>Wide Renge Of Properties</h4>
                            <p>Aliquam dictum elit vitae mauris facilisis at dictum urna dignissim donec vel lectus vel felis.</p>
                        </div>
                    </div>
                </div>
                <div className="col-md-6 col-lg-4 col-xl-4">
                    <div className="why_chose_us style2">
                        <div className="icon">
                            <span className="flaticon-profit"></span>
                        </div>
                        <div className="details">
                            <h4>Financing Made Easy</h4>
                            <p>Aliquam dictum elit vitae mauris facilisis at dictum urna dignissim donec vel lectus vel felis.</p>
                        </div>
                    </div>
                </div>
            </div>
        </div>
    </section>
 )
}
export default AboutSection